import React, { useState, useEffect } from 'react';

const DogList = () => {
    const [dogList, setDogList] = useState([]);

    useEffect(() => {
        fetch('http://localhost/path/to/your/doglist-api.php') // Update the URL
            .then(response => response.json())
            .then(data => setDogList(data))
            .catch(error => console.error('Error fetching dog list:', error));
    }, []);

    return (
        <div>
            <h2>Dog List</h2>
            <div className="card-container d-flex flex-wrap text-center justify-content-center">
                {dogList.map(dog => (
                    <div key={dog.id} className="card bg-primary" style={{ height: '300px', width: '200px' }}>
                        <img className="card-img-top" src={dog.image} alt={dog.name} style={{ height: '200px', width: '200px' }} />

                        <div className="card-body">
                            <h3 className="card-title">{dog.name}</h3>
                            <p className="card-text">{dog.type}</p>
                            {/* Add other fields as needed */}
                        </div>
                    </div>
                ))}
            </div>
        </div>
    );
};

export default DogList;
