import React from 'react';
import Button from 'react-bootstrap/Button';
import Card from 'react-bootstrap/Card';
import { Link } from 'react-router-dom';

function BasicExample() {
 
  const cardContent = [
    {
      title: 'DONATE',
      text: 'As a non-profit organization, Fuzzy Paws operates and relies heavily on donations from the community. We are always called upon to rescue animals suffering from abandonment, abuse, or injury and none of it is possible without your support.',
      buttonText: 'DONATE',
      imageUrl: 'https://images.pexels.com/photos/1751541/pexels-photo-1751541.jpeg?auto=compress&cs=tinysrgb&w=600',
      link: '/donate',
      gradient: ' linear-gradient( rgba(151, 193, 255, 0), rgb(243, 150, 50))',
    },
    {
      title: 'ADOPT',
      text: 'Many of our Adoptables from our shelters have been rescued from death row, from local pounds or from abusive environments. By choosing to adopt, you are giving them the chance to find a new home and to feel the love and security they deserve.',
      buttonText: 'ADOPT',
      imageUrl: 'https://images.pexels.com/photos/1938126/pexels-photo-1938126.jpeg?auto=compress&cs=tinysrgb&w=600',
      link: '/adopt',
      gradient: 'linear-gradient( rgba(151, 193, 255, 0), #50b3f3)',
    },
    {
      title: 'SPONSOR',
      text: 'While not everyone can foster or adopt, there are always other ways to help our rescues. Through our Sponsor-A-Rescue Program, our rescues now have a wonderful support system rooting for their full recovery and continuous comfort while at the shelter.',
      buttonText: 'SPONSOR',
      imageUrl: 'https://images.pexels.com/photos/1851188/pexels-photo-1851188.jpeg?auto=compress&cs=tinysrgb&w=600',
      link: '/sponsor',
      gradient: ' linear-gradient( rgba(151, 193, 255, 0), #f1807c)',
    },
  ];

  return (
    <div className="container">
      <div className="row row-cols-1 row-cols-md-3 g-4">
        {cardContent.map((card, index) => (
          <div key={index} className="col">
            <Card className="home-card text-light border-0 rounded-3" style={{ height: '31rem', backgroundImage: `${card.gradient}, url(${card.imageUrl})`, backgroundSize: 'cover' }}>
              <Card.Body className="text-center pt-5 mt-5">
                <Card.Title className="fs-4" style={{ color: 'orange' }}><b>{card.title}</b></Card.Title>
                <Card.Text>{card.text}</Card.Text>
                <Link to={card.link}>
                    <Button className="card-btn border-0 rounded-3"><b>{card.buttonText}</b></Button>
                </Link>
              </Card.Body>
            </Card>
          </div>
        ))}
      </div>
    </div>
  );
}

export default BasicExample;
