import React, { useState, useRef, useEffect, useContext } from 'react';
import { useNavigate, Link } from 'react-router-dom';
import axios from 'axios';

function Register() {
  const navigate = useNavigate();

  const [fullname, setFullname] = useState("");
  const [email, setEmail] = useState("");
  const [contactNumber, setContactNumber] = useState("");
  const [zipcode, setZipcode] = useState("");
  const [password, setPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");
  const [fullnameError, setFullnameError] = useState("");
  const [emailError, setEmailError] = useState("");
  const [contactNumberError, setContactNumberError] = useState("");
  const [zipcodeError, setZipcodeError] = useState("");
  const [passwordError, setPasswordError] = useState("");
  const [confirmPasswordError, setConfirmPasswordError] = useState("");
  const inputReference = useRef();
  const url = 'http://localhost/capstone2/register.php';

  const fullnameVerify = () => {
    if (fullname.length >= 2) {
      setFullnameError("");
      return true;
    }
    setFullnameError("Full name must be at least 2 characters");
    return false;
  };

  const emailVerify = () => {
    const emailPattern = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    if (emailPattern.test(email)) {
      setEmailError("");
      return true;
    }
    setEmailError("Invalid email format");
    return false;
  };

  const contactNumberVerify = () => {
    setContactNumberError("");
    return true;
  };

  const zipcodeVerify = () => {
    setZipcodeError("");
    return true;
  };

  const passwordVerify = () => {
    if (password.length >= 10) {
      setPasswordError("");
      return true;
    }
    setPasswordError("Password must be at least 10 characters");
    return false;
  };

  const confirmPasswordVerify = () => {
    if (confirmPassword === password) {
      setConfirmPasswordError("");
      return true;
    }
    setConfirmPasswordError("Passwords do not match");
    return false;
  };

  const validated = () => {
    const isFullnameValid = fullnameVerify();
    const isEmailValid = emailVerify();
    const isContactNumberValid = contactNumberVerify();
    const isZipcodeValid = zipcodeVerify();
    const isPasswordValid = passwordVerify();
    const isConfirmPasswordValid = confirmPasswordVerify();

    return (
      isFullnameValid &&
      isEmailValid &&
      isContactNumberValid &&
      isZipcodeValid &&
      isPasswordValid &&
      isConfirmPasswordValid
    );
  };

  const submitForm = (e) => {
    e.preventDefault();

    if (validated()) {
      let code = Math.random();
      sessionStorage.setItem("token", code);

      let getData = new FormData();
      getData.append('fullname', fullname);
      getData.append('email', email);
      getData.append('contactNumber', contactNumber);
      getData.append('zipcode', zipcode);
      getData.append('password', password);

      getData.append('function', 'insert');

      axios({
        url: url,
        method: 'POST',
        data: getData,
        config: { headers: { 'Content-Type': 'multipart/form-data' } },
      })
        .then((response) => {
          alert('Registration successful!');
          navigate('/login');
        })
        .catch((error) => {
          console.error("Registration failed:", error);
        });
    } else {
      alert("Please enter valid information");
    }
  };

  useEffect(() => {
    // axios.get(url).then((response) => {
    //     setStudents(response.data);
    //   });
  }, []);

  return (
    <div>
      <form className="text-center p-5 wrapper">
        <h1 className="display-6"><b>REGISTER</b></h1>
        <label className="my-3 fs-5">Full Name: </label> <br />
        <input
          ref={inputReference}
          className="rounded-3 p-2 "
          type="text"
          id="fullname"
          autoComplete="off"
          value={fullname}
          onChange={(e) => { setFullname(e.target.value); }}
          onBlur={fullnameVerify}
        /> <br /><br />

        {fullnameError && <p style={{ color: 'red' }}>{fullnameError}</p>}

        <label className="my-3 fs-5">Email: </label> <br />
        <input
          className="rounded-3 p-2 "
          type="email"
          id="email"
          autoComplete="off"
          value={email}
          onChange={(e) => { setEmail(e.target.value); }}
          onBlur={emailVerify}
        /> <br /><br />

        {emailError && <p style={{ color: 'red' }}>{emailError}</p>}

        <label className="my-3 fs-5">Contact Number: </label> <br />
        <input
          className="rounded-3 p-2 "
          type="number"
          id="contactNumber"
          autoComplete="off"
          value={contactNumber}
          onChange={(e) => { setContactNumber(e.target.value); }}
          onBlur={contactNumberVerify}
        /> <br /><br />

        {contactNumberError && <p style={{ color: 'red' }}>{contactNumberError}</p>}

        <label className="my-3 fs-5">Zipcode: </label> <br />
        <input
          className="rounded-3 p-2 "
          type="number"
          id="zipcode"
          autoComplete="off"
          value={zipcode}
          onChange={(e) => { setZipcode(e.target.value); }}
          onBlur={zipcodeVerify}
        /> <br /><br />

        {zipcodeError && <p style={{ color: 'red' }}>{zipcodeError}</p>}

        <label className="my-3 fs-5">Password: </label> <br />
        <input
          className="rounded-3 p-2"
          type="password"
          id="password"
          autoComplete="off"
          value={password}
          onChange={(e) => { setPassword(e.target.value); }}
          onBlur={passwordVerify}
        /> <br /> <br />

        {passwordError && <p style={{ color: 'red' }}>{passwordError}</p>}

        <label className="my-3 fs-5">Confirm Password: </label> <br />
        <input
          className="rounded-3 p-2"
          type="password"
          id="confirmPassword"
          autoComplete="off"
          value={confirmPassword}
          onChange={(e) => { setConfirmPassword(e.target.value); }}
          onBlur={confirmPasswordVerify}
        /> <br /> <br />

        {confirmPasswordError && <p style={{ color: 'red' }}>{confirmPasswordError}</p>}

        <button className="my-btn btn text-light my-3 fs-5" type="button" onClick={submitForm}>Register</button> <br />

        <Link id="nav-title" className="nav-title navbar-brand fs-5" to="/login">
          <button className="my-btn btn text-light my-3 fs-5" type="button">Login</button>
        </Link>
      </form>
    </div>
  );
}

export default Register;
