
import React from 'react';
import {useContext, useEffect} from 'react';
import {Routes, Route, useNavigate} from 'react-router-dom';

function Banner () {

    const navigate = useNavigate();

    const navigateAdopt = () => {
      navigate('/adopt');
    };

    return(
        <div>
            
            <div className="container-fluid bg-overlay2 d-flex align-items-center justify-content-center">
                <div className="row text-center mx-auto my-auto" style={{float: "center"}}>
                    <div className="text-home p-5">
                        <h3 className="my-3">Don't shop. <span style={{textDecoration: "underline"}}>Adopt </span> your new best friend!</h3> 
                        <button type="button" onClick={navigateAdopt} className="btn adopt-btn fs-5 w-50 my-3 rounded-4" >Adopt</button>
                    </div>
            
                </div>
            </div>

        </div>
    )
}

export default Banner;