import {Link} from 'react-router-dom';
// import {useNavigate} from 'react-router-dom';

import React, { useState, useEffect } from 'react';
import Button from 'react-bootstrap/Button';
import Collapse from 'react-bootstrap/Collapse';

import UserContext from '../pages/data';
import {useContext} from 'react';

const Nav = () => {
  
  // Toggle btn mobile
  const [isMobileMenuOpen, setMobileMenuOpen] = useState(false);
    const toggleMobileMenu = () => {
      setMobileMenuOpen(!isMobileMenuOpen);
    };

  // Logout btn
  // let navigate = useNavigate();
  //   const logout = () => {
  //     sessionStorage.removeItem("token");
  //     navigate('/login');
  //   }
  
  // Change nav color
  const [isScrolled, setIsScrolled] = useState(false);
  const [showLogo, setShowLogo] = useState(true);
    useEffect(() => {
      const changeColor = () => {
        const scrollTop = window.scrollY;
        setIsScrolled(scrollTop >= 140);
      };
    
      window.addEventListener('scroll', changeColor);
        
      return () => {
        window.removeEventListener('scroll', changeColor);
      };
    }, []);
  
  // Search bar animation
  const [open, setOpen] = useState(false);

  let {user, setUser} = useContext(UserContext);

  let changeName = () => {
      let fname = document.getElementById("fname").value;
      setUser(fname);
  }


    return(
      <div>
          
          {/* HEADER */}
          <div className="my-navbar header">
            
            <div className="d-flex justify-content-between align-items-center p-4">
              
              <form class="d-flex" role="search">
                {/* <button className="btn" type="submit"> <i id="nav-icon" className=" bi bi-search"></i></button> */}
                <Button id="search-btn" className="btn shadow-none" onClick={() => setOpen(!open)}aria-controls="example-collapse-text" aria-expanded={open}>
                  <i id="nav-icon" className="bi bi-search "></i>
                </Button>
                <Collapse in={open}>
                  <div id="example-collapse-text">
                    <input id="search-input" className="form-control shadow-none border-0" type="search" placeholder="Search" aria-label="Search"/>
                  </div>
                </Collapse>
              </form>

              <div className="flex-grow-1 text-center">
                    {/* <img src="https://cdn-icons-png.flaticon.com/128/1279/1279250.png" className="mb-2 me-3" alt="logo" style={{width: "60px", height: "60px"}}/> */}
                    <img src="https://cdn-icons-png.flaticon.com/128/5308/5308461.png" className="mb-2 me-3" alt="logo" style={{width: "60px", height: "60px"}}/>
                    <Link id="nav-title" className="nav-title navbar-brand fs-5" to="/home">
                      <b>Fuzzy Paws</b>
                    </Link>
              </div>

    
              <div style={{display: "flex", justifyContent: "space-between"}}>
                {/* <p className="text-center mt-2 welcome-nav">Hi, {user}!</p> <br/> <br/>  */}
                <Link className="nav-link mt-1" aria-current="page" to="/login"><i id="nav-icon" className="nav-icon bi bi-person fs-5"></i></Link>
              </div>

            </div>
          </div>

          {/* NAVBAR*/}
          <nav className={`my-navbar navbar navbar-expand-md py-3 ps-3 ${isScrolled ? 'scrolled' : ''}`}  >
            <div className="text-center">
                <div id="toggler-btn">
                  <button className={`navbar-toggler ${isMobileMenuOpen ? 'collapsed' : ''} border-0 shadow-none`} type="button" data-toggle="collapse" data-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation" onClick={toggleMobileMenu}>
                  <span className="navbar-toggler-icon "></span>
                  </button>
                  </div>

                <div className={`collapse navbar-collapse ${isMobileMenuOpen ? 'show' : ''}`} id="navbarNav">
                  <ul className="navbar-nav mb-lg-0">
                    <li className="nav-item">
                      <Link className="nav-link mx-5" aria-current="page" to="/home">Home</Link>
                    </li>
                  
                    <li className="nav-item dropdown open">
                      <Link className="nav-link mx-5 dropdown-toggle" to="/about" role="button" data-bs-toggle="dropdown">
                      About
                      </Link>
                      <ul className="dropdown-menu container-fluid rounded-4" style={{backgroundColor:  "#f5ece4ff"}}>
                          <li><Link className="dropdown-item" to="/whowe">Who We Are</Link></li>
                          <li><Link className="dropdown-item" to="/whatwe">What We Do</Link></li> 
                          <li><Link className="dropdown-item" to="/shelters">Our Shelters</Link></li> 

                      </ul> 
                    </li>

                    <li className="nav-item">
                      <Link className="nav-link mx-5" to="/adopt">Adopt</Link>
                    </li>

                    {/* <li className="nav-item">
                      <Link className="nav-link" to="/contact">Get Involved</Link>
                    </li> */}

                    <li className="nav-item dropdown">
                        <Link className="nav-link mx-5 dropdown-toggle" to="/involve" role="button" data-bs-toggle="dropdown">
                        Get Involved
                        </Link>
                            <ul className="dropdown-menu rounded-4" style={{backgroundColor:  "#f5ece4ff"}}>
                              <li><Link className="dropdown-item" to="/adoptprocess">Foster</Link></li>
                              <li><Link className="dropdown-item" to="/whatwe">Volunteer</Link></li> 
                              <li><Link className="dropdown-item" to="/shelters">Donate</Link></li> 
                            </ul> 
                    </li> 

                    <li className="nav-item">
                      <Link className="nav-link mx-5" to="/contact">Contact</Link>
                    </li>

                  </ul>
                    {/* <form className="d-flex" role="search">
                      <button type='button' onClick={logout} className="btn btn-outline-light my-btn" >Logout</button>
                    </form> */}
                </div>
            </div>
          </nav>
          <br/> <br/> 
          
          
      </div>
    )
}

export default Nav;

