// AdoptionPage.js
import React, { useState, useEffect } from 'react';
import axios from 'axios';
import DogList from '../components/DogList';
import SearchList from '../components/SearchList';

const AdoptionPage = () => {
  const [dogs, setDogs] = useState([]);
  const [filteredDogs, setFilteredDogs] = useState([]);

  useEffect(() => {
    // Fetch data from the PHP script
    axios.get('http://localhost/capstone2/dog.php').then((response) => {
      setDogs(response.data);
      setFilteredDogs(response.data);
    });
  }, []);

  const handleSearch = (searchTerm) => {
    // Perform search based on the searchTerm and update filteredDogs
    const filteredResults = dogs.filter(
      (dog) =>
        dog.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
        dog.type.toLowerCase().includes(searchTerm.toLowerCase())
    );
    setFilteredDogs(filteredResults);
  };

  return (
    <div>
      <SearchList onSearch={handleSearch} />
      <DogList dogs={filteredDogs} />
    </div>
  );
};

export default AdoptionPage;
