// DogList.js

import React, { useState, useEffect } from 'react';

const DogList = () => {
    const [dogList, setDogList] = useState([]);
    const [searchCriteria, setSearchCriteria] = useState('');

    useEffect(() => {
        fetch('http://localhost/capstone2/dog.php') // Replace with your API URL
            .then(response => response.json())
            .then(data => setDogList(data))
            .catch(error => console.error('Error fetching dog list:', error));
    }, []);

    const handleSearch = () => {
        fetch('http://localhost/capstone2/dog.php', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded',
            },
            body: `function=search&searchCriteria=${searchCriteria}`,
        })
            .then(response => response.json())
            .then(data => setDogList(data))
            .catch(error => console.error('Error fetching search results:', error));
    };

    return (
        <div>
            <h2>Dog List</h2>
            <div>
                <input
                    type="text"
                    value={searchCriteria}
                    onChange={e => setSearchCriteria(e.target.value)}
                />
                <button onClick={handleSearch}>Search</button>
            </div>
            <ul>
                {dogList.map(dog => (
                    <li key={dog.id}>{dog.name}</li>
                ))}
            </ul>
        </div>
    );
};

export default DogList;
