import React from 'react';
import CountUp from 'react-countup';
import { useInView } from 'react-intersection-observer';
import { useSpring, animated } from 'react-spring';

const Counter = () => {
  const [ref1, inView1] = useInView({
    triggerOnce: true,
  });

  const [ref2, inView2] = useInView({
    triggerOnce: true,
  });

  const [ref3, inView3] = useInView({
    triggerOnce: true,
  });

  const [ref4, inView4] = useInView({
    triggerOnce: true,
  });

  const fadeInUp1 = useSpring({
    opacity: inView1 ? 1 : 0,
    transform: inView1 ? 'translateY(0)' : 'translateY(50px)',
    from: { opacity: 0, transform: 'translateY(50px)' },
    delay: 0,
  });

  const fadeInUp2 = useSpring({
    opacity: inView2 ? 1 : 0,
    transform: inView2 ? 'translateY(0)' : 'translateY(50px)',
    from: { opacity: 0, transform: 'translateY(50px)' },
    delay: 400,
  });

  const fadeInUp3 = useSpring({
    opacity: inView3 ? 1 : 0,
    transform: inView3 ? 'translateY(0)' : 'translateY(50px)',
    from: { opacity: 0, transform: 'translateY(50px)' },
    delay: 800,
  });

  const fadeInUp4 = useSpring({
    opacity: inView4 ? 1 : 0,
    transform: inView4 ? 'translateY(0)' : 'translateY(50px)',
    from: { opacity: 0, transform: 'translateY(50px)' },
    delay: 1600,
  });

  return (
    <div className="counter container rounded-4 p-5" style={{ textAlign: "center" }}>
      <div className="row ">
        <animated.div className="col-md-3 p-3"
          ref={ref1}
          style={{
            // spread operator spreadz  properties fadeInUp1 obj into the style obj 
            ...fadeInUp1,
            transition: 'opacity 0.5s, transform 0.5s',
          }}
        >
          {inView1 && (
            <h3 style={{ display: "inline-block", textAlign: "left" }}>
              <img className="my-3" src="https://cdn-icons-png.flaticon.com/128/2143/2143047.png" /><br />
              <CountUp start={0} end={750} duration={2} />
              <br /><br />
              RESCUED
            </h3>
          )}
        </animated.div>
        <animated.div className="col-md-3 p-3"
          ref={ref2}
          style={{
            ...fadeInUp2,
            transition: 'opacity 0.5s, transform 0.5s',
          }}
        >
          {inView2 && (
            <h3 style={{ display: "inline-block", textAlign: "left" }}>
              <img className="my-3" src="https://cdn-icons-png.flaticon.com/128/1303/1303467.png" /><br />
              <CountUp start={0} end={3200} duration={2} />
              <br /><br />
              NEUTERED
            </h3>
          )}
        </animated.div>
        <animated.div className="col-md-3 p-3"
          ref={ref3}
          style={{
            ...fadeInUp3,
            transition: 'opacity 0.5s, transform 0.5s',
          }}
        >
          {inView3 && (
            <h3 style={{ display: "inline-block", textAlign: "left" }}>
              <img className="my-3" src="https://cdn-icons-png.flaticon.com/128/2143/2143069.png" /><br />
              <CountUp start={0} end={2800} duration={2} />
              <br /><br />
              SHELTERED
            </h3>
          )}
        </animated.div>
        <animated.div className="col-md-3 p-3"
          ref={ref4}
          style={{
            ...fadeInUp4,
            transition: 'opacity 0.5s, transform 0.5s',
          }}
        >
          {inView4 && (
            <h3 style={{ display: "inline-block", textAlign: "left" }}>
              <img className="my-3" src="https://cdn-icons-png.flaticon.com/128/2913/2913369.png" /><br />
              <CountUp start={0} end={1500} duration={2} />
              <br /><br />
              REHOMED
            </h3>
          )}
        </animated.div>
      </div>
    </div>
  );
};

export default Counter;
